'use strict';

var Sugar = require('sugar-core'),
    ALL_ZENKAKU_REG = require('../language/var/ALL_ZENKAKU_REG'),
    convertCharacterWidth = require('../language/internal/convertCharacterWidth');

Sugar.String.defineInstance({

  'hankaku': function(str, mode) {
    return convertCharacterWidth(str, mode, ALL_ZENKAKU_REG, 'hankaku');
  }

});

module.exports = Sugar.String.hankaku;